 

*,*::before,*::after {
 box-sizing: border-box;
}

body{
  margin: 0;
  background:linear-gradient(to right,#ff92ff,#7112ff);
 border: 2px solid rgb(227, 218, 218);
 height: 100vh;
 display: flex;
 justify-content: center;
 align-items: center;
 text-align: center;
   
 
}
.text-center{
  font-size: 2rem;
  color: white;
  text-shadow:  5px 5px 4px rgba(0, 0, 0, 0.5);

  ;
}
.calculator-grid {
  display: grid;
grid-template-columns: repeat(4,6rem);
justify-content: center;
grid-template-rows: minmax(7rem , auto)repeat(5,6rem);
}
.calculator-grid >button{
  cursor: pointer;
  font-size: 2rem;
  border: 1px solid white;
  outline: none;
  background-color: rgba(12, 14, 67, 0.75);
}
.calculator-grid > button:hover,
.calculator-grid > button:focus{
  background-color: rgba(255, 255, 255, 0.9);
}
.span-two{
  grid-column: span 2;
}
.output {
  grid-column: 1/-1;
 background-color: rgb(102, 60, 60);
 display: flex;
 flex-direction: column;
 align-items: flex-end;
 padding: 0.75rem;
 word-wrap: break-word;
 word-break: break-all;
 
}
.output .previous-oprands{
  color: rgba(255, 255, 255 ,0.75);
  font-size: 1rem;
}
.output .current-oprands{
  color: rgba(255, 255, 255 ,1);
  font-size: 2.5rem;
}